html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Lora";
  font-style: normal;
  font-weight: 700;
  src: local("Lora Bold"), local("lora-bold"), url("../fonts/Lora-Bold.woff2") format("woff2"), url("../fonts/Lora-Bold.woff") format("woff");
}

@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 300;
  src: local("Geometria Light"), local("geometria-light"), url("../fonts/Geometria-Light.woff2") format("woff2"), url("../fonts/Geometria-Light.woff") format("woff");
}

@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 400;
  src: local("Geometria"), local("geometria"), url("../fonts/Geometria.woff2") format("woff2"), url("../fonts/Geometria.woff") format("woff");
}

@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 700;
  src: local("Geometria Bold"), local("geometria-bold"), url("../fonts/Geometria-Bold.woff2") format("woff2"), url("../fonts/Geometria-Bold.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  line-height: 1.4;
  font-family: 'Geometria', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #000;
  min-width: 320px;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 1023px) {
  body {
    font-size: 12px;
  }
}

a {
  text-decoration: none;
  color: currentColor;
}

.no-touch a:hover {
  text-decoration: none;
}

.out {
  overflow: hidden;
  position: relative;
}

.inner {
  max-width: 1200px;
  padding: 0 30px;
  margin: 0 auto;
}

@media only screen and (max-width: 767px) {
  .inner {
    padding: 0 15px;
  }
}

.header {
  padding: 70px 0;
}

@media only screen and (max-width: 1259px) {
  .header {
    padding: 40px 0;
  }
}

@media only screen and (max-width: 767px) {
  .header {
    padding: 20px 0;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .header__inner {
    flex-wrap: wrap;
  }
}

.header__logo {
  margin-right: 28px;
}

@media only screen and (max-width: 1023px) {
  .header__logo {
    margin-right: 16px;
  }
}

.header__left {
  display: flex;
  align-items: center;
  padding-right: 30px;
}

@media only screen and (max-width: 767px) {
  .header__left {
    padding: 0;
    font-size: 0;
  }
}

.header__right {
  display: flex;
  align-items: center;
  padding-left: 30px;
}

@media only screen and (max-width: 767px) {
  .header__right {
    flex-direction: column;
  }
}

.header__tel {
  font-size: 18px;
  font-weight: 700;
}

@media only screen and (max-width: 1023px) {
  .header__tel {
    font-size: 14px;
  }
}

@media only screen and (max-width: 767px) {
  .header__tel {
    padding-bottom: 3px;
  }
}

.header__btn {
  margin-left: 15px;
}

@media only screen and (max-width: 767px) {
  .header__btn {
    margin: 0;
  }
}

.logo {
  display: block;
  width: 68px;
  height: 68px;
  background-color: #df5959;
  text-align: center;
  line-height: 68px;
  font-size: 24px;
  font-weight: 700;
  color: #fff;
}

@media only screen and (max-width: 1023px) {
  .logo {
    width: 54px;
    height: 54px;
    font-size: 20px;
    line-height: 54px;
    flex-shrink: 0;
  }
}

.btn {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #df5959;
  padding: 13px 26px;
  font-size: 14px;
  line-height: 1;
  font-family: 'Geometria', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  border-radius: 30px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .3s, box-shadow .3s;
  cursor: pointer;
}

.no-touch .btn:hover {
  background-color: #db4444;
  box-shadow: none;
}

@media only screen and (max-width: 1023px) {
  .btn {
    font-size: 12px;
    padding: 10px 20px;
  }
}

.btn_lg {
  font-size: 16px;
  font-weight: 700;
  padding: 22px 30px;
  box-shadow: 0px 15px 20px 0px #e6d3d9;
}

@media only screen and (max-width: 1023px) {
  .btn_lg {
    font-size: 14px;
    padding: 14px 20px;
  }
}

@media only screen and (max-width: 767px) {
  .btn_lg {
    padding: 14px 20px;
  }
}

.btn_orange {
  background-color: #df9534;
}

.hero {
  font-size: 16px;
  line-height: 2;
  padding: 46px 0;
  line-height: 1.5;
}

@media only screen and (max-width: 1259px) {
  .hero {
    padding: 30px 0;
    font-size: 14px;
  }
}

@media only screen and (max-width: 1023px) {
  .hero {
    padding: 20px 0;
  }
}

.hero__inner {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 1023px) {
  .hero__inner {
    flex-wrap: wrap;
  }
}

.hero__left {
  width: 50%;
  padding-right: 50px;
}

@media only screen and (max-width: 1023px) {
  .hero__left {
    width: 100%;
    padding: 0 0 40px;
  }
}

.hero__left p {
  margin-top: 22px;
}

@media only screen and (max-width: 767px) {
  .hero__left p {
    margin-top: 15px;
  }
}

.hero__title span {
  color: #df5959;
}

.hero__btn {
  margin-top: 65px;
}

.hero__btn button {
  min-width: 265px;
}

@media only screen and (max-width: 1259px) {
  .hero__btn {
    margin-top: 40px;
  }
}

@media only screen and (max-width: 1023px) {
  .hero__btn {
    text-align: center;
  }
}

@media only screen and (max-width: 767px) {
  .hero__btn {
    margin-top: 20px;
  }
}

.hero__right {
  width: 50%;
  background-repeat: no-repeat;
  background-position: top left;
  background-size: contain;
  padding-left: 270px;
  padding-bottom: 20px;
}

@media only screen and (max-width: 1259px) {
  .hero__right {
    padding-left: 250px;
    padding-bottom: 140px;
  }
}

@media only screen and (max-width: 1023px) {
  .hero__right {
    width: 100%;
    background-size: 560px 360px;
    padding-left: 270px;
    padding-bottom: 280px;
  }
}

@media only screen and (max-width: 599px) {
  .hero__right {
    padding-left: 123px;
    padding-bottom: 90px;
    background-size: 255px 180px;
    background-position: bottom left;
  }
}

.hero__right p {
  font-size: 20px;
}

@media only screen and (max-width: 1259px) {
  .hero__right p {
    font-size: 16px;
  }
}

@media only screen and (max-width: 599px) {
  .hero__right p {
    font-size: 14px;
  }
}

.hero__caption {
  color: #df5959;
  text-align: right;
  margin-top: 7px;
}

.complex {
  padding: 100px 0;
}

.complex__title {
  margin-top: 4px;
}

.complex__descr {
  max-width: 750px;
  font-size: 16px;
  line-height: 1.5;
  margin-top: 14px;
}

.complex__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 -15px;
  padding-left: 8px;
}

.complex__item {
  width: 50%;
  padding: 70px 15px 0;
}

.complex__btn {
  text-align: center;
  margin-top: 65px;
}

.complex__btn button {
  min-width: 420px;
}

.exercise {
  width: 100%;
  max-width: 480px;
  display: flex;
  align-items: flex-end;
  min-height: 100px;
}

.exercise__img {
  min-height: 100px;
  max-width: 100px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 15px;
  margin-right: 22px;
}

.exercise__img img {
  display: block;
  width: 100%;
  max-width: 64px;
}

.exercise p {
  margin-top: 8px;
  line-height: 1.8;
}

.title {
  font-family: 'Geometria', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 700;
  color: #000;
}

.title_main {
  font-size: 20px;
  line-height: 1.2;
}

@media only screen and (max-width: 1023px) {
  .title_main {
    font-size: 16px;
  }
}

.title_lg {
  font-size: 48px;
  line-height: 1.25;
}

@media only screen and (max-width: 1259px) {
  .title_lg {
    font-size: 38px;
  }
}

.title_md {
  font-size: 30px;
  font-weight: 400;
}

.title_md span {
  font-weight: 700;
}

.title_sm {
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
  color: #df5959;
}

.subtitle {
  font-size: 14px;
  font-weight: 300;
  color: #df5959;
  text-transform: uppercase;
}
